	module.exports = {"data":{"challengeNode":{"title":"Build a Roguelike Dungeon Crawler Game","description":["<strong>目标：</strong>在 <a href='https://codepen.io' target='_blank'>CodePen.io</a> 平台开发一个功能类似于<a href='https://codepen.io/freeCodeCamp/full/apLXEJ/  target='_blank'>这个示例 app </a>的应用。","满足下列<a href='https://en.wikipedia.org/wiki/User_story' target='_blank'>需求</a>。你可以使用任何你需要的库或 API。发挥你自己的代码风格。","<strong>需求：</strong>我有生命值、等级、一个武器。我能捡到其他更好的武器。我能捡到补血药。","<strong>需求：</strong>所有的（可拾起的）物品和敌人都在地图上随机分布。","<strong>需求：</strong>我能游览整个地图，寻找物品。","<strong>需求：</strong>我能在地图范围内自由移动，但是遇到敌人时，我只能打败敌人后才能继续往前走。","<strong>需求：</strong>地图的绝大部分都被遮挡，当我往前走时，地图才会显示剩下的部分。","<strong>需求：</strong>打败敌人能获得经验值，而积攒经验值能够让我升级。","<strong>需求：</strong>交战时，大家轮流攻击对方，直到我们中的一个死掉。我的攻击力取决于我的等级和武器。敌人的攻击力以其等级为基准在一定范围内随机。","<strong>需求：</strong>当我打败大 boss 后，我就胜利了。","<strong>需求：</strong>游戏应当具备挑战性，但理论上是可以获得胜利的。","如果你遇到了问题，请点击 <a href='https://forum.freecodecamp.one/t/topic/157' target='_blank'>帮助</a>。","当你完成时，请点击下方的 \"我已经完成了这个挑战\" 按钮并输入你的 CodePen 链接。 ","你可以通过将你的项目分享到社交网络来获得反馈。"],"challengeType":3,"fields":{"blockName":"Take Home Projects","slug":"/coding-interview-prep/take-home-projects/build-a-roguelike-dungeon-crawler-game"}}},"pathContext":{"challengeMeta":{"introPath":"","template":null,"required":[],"nextChallengePath":"/coding-interview-prep/take-home-projects/p2p-video-chat-application","id":"bd7153d8c242eddfaeb5bd13"},"slug":"/coding-interview-prep/take-home-projects/build-a-roguelike-dungeon-crawler-game"}}

